.spinner {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

#spinner-container {
    position: fixed;
    background-color: white;
    width: 200vw;
    height: 200vh;
    display: flex;

    align-items: center;
    justify-content: center;

    transition-duration: .3s;
}

.spinner div {
    display: inline-block;
    position: absolute;
    left: 8px;
    width: 16px;
    background: orange;
    animation: spinner 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}

.spinner div:nth-child(1) {
    left: 8px;
    animation-delay: -0.24s;
}
.spinner div:nth-child(2) {
    left: 32px;
    animation-delay: -0.12s;
}

.spinner div:nth-child(3) {
    left: 56px;
    animation-delay: 0;
}

@keyframes spinner {
0% {
    top: 8px;
    height: 64px;
}

50%, 100% {
    top: 24px;
    height: 32px;
}
}
